/*------------------------------------*\
    $TABLES
\*------------------------------------*/

table {
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid $border-color;
  width: 100%;
}
th {
  text-align: left;
  border: 1px solid $border-color;
  padding: 0.2em;
}
td {
  border: 1px solid $border-color;
  padding: 0.2em;
}

/**
 * Table Layout
 */
.t {
  display: table;
}
.t-cell {
  display: table-cell;
}
